@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
body {
  margin: auto;
  font-family: 'Acme', sans-serif;


  background-color: #a8d0e6;
  min-width: 450px;
  min-height: 100%;
  margin-left: 10px;
  margin-right: 10px;
  /* background-image: url(image/background.jpg); */
  /* background-size: cover; */
  /* padding: 10px; */
}


.links, .user{
  text-decoration: none;
  color: white;
  margin: 10px;
  font-size: 1.3em;
}
.links:hover, .user:hover {
  color: #f8e9a1;
}

.showpage {
  margin: 0 auto;
  width: 100%;
  height: auto;
  border: 1px solid black;
  border: 10px solid #374785;
  border-radius: 10px;
}

.showpage > img {
  display: block;
  width: 100%;
  object-fit: cover;
}

h3 {
  padding-left: 10px;
  font-size: 1.5em;
  color: #f8e9a1;
}

p {
  margin: 0 auto;
  font-size: 1.5em;
  text-align: justify;

  padding-left: 10px;
  margin-bottom: 15px;
}
.intro {
  font-size:1.5em;
  color: #f76c6c;
}

.container {
  margin: auto;
  text-align: center;
  /* border: 1px solid black; */
}

.title, .roomTitle{
  color: #f76c6c;
}
.roomTitle {
  font-size: 2em;
}


.playrooms-container {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.playrooms {
  margin: 30px;
  border-radius: 5px;
  box-shadow: 0 0 0 10px #374785;
  width: 300px;
  text-align: center;
  /* margin: 15px auto; */
  /* margin-bottom: 15vh; */
}
.drawer-guesser-container {
  display: flex;
  flex-direction: row;
  /* border: 1px solid black; */
  /* height: 30vh; */
  justify-content: space-between;
  margin: 10px auto;
}

.drawer-container, .guesser-container{
  position: relative;
  width: 50%;
  border-top: 6px solid #f8e9a1;
}
