}
.drawer, .guesser {
  position: relative;
  margin: 5px auto;
  width: 60px;
  height: 40px;
  border-radius: 10px;
}

.error-message {
  margin-left: 10px;
  /* color: #f76c6c; */
}

#loginLink {
  text-decoration: none;
  color: #374785;
}

#loginLink:hover {
  color: #f8e9a1;
}

.start {
  color: #f76c6c;
  font-family: 'Acme', sans-serif;
  font-size: 1em;
  margin: 10px;
  border-radius: 5px;
  box-shadow: 0 0 0 5px #374785;
}

.start:hover{
  background-color: #f8e9a1;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}



.login {
  width: 400px;
  height: 600px;
  margin: 80px auto;
  text-align: center;
  font-size: 1.5em;
  border-radius: 10px;
  background-image: url('https://images.pexels.com/photos/1762851/pexels-photo-1762851.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260');
  background-size: cover;
}

.login > h1 {
  padding-top: 100px;
  color: #24305e;
}

.login > form >input {
  margin: 10px;
  height: 40px;
  width: 300px;
  border-radius: 5px;
  font-size: 1em;
  color: #374785;
  font-family: 'Acme', sans-serif;
  background-color:rgba(83, 78, 79, 0.49);
}

.submit {
  width: 100px;
  height: 20px;
  background-color: #24305e;
  font-size: 1em;
  font-family: 'Acme', sans-serif;
  color: white;
  border-radius: 5px;
}
.submit:hover{
  color: #f8e9a1;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.or {
  margin-top: 10px;
}

.or > span {
  color: #f76c6c;
}
.or > .signup {
  padding-left: 3px;
  text-decoration: none;
  color: white;
}
.or > .signup:hover {
  color: #f8e9a1;
}

.sign {
  width: 500px;
  height: 600px;
  margin: 80px auto;
  text-align: center;
  font-size: 1.5em;
  border-radius: 10px;
  background-image: url('https://images.pexels.com/photos/1762851/pexels-photo-1762851.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260');
  background-size: cover;
}

.sign > h1 {
  padding-top: 50px;
  color: #24305e;
}

