  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-right:
}
.clear, .undo{
  margin: 5px;
  width: 30px;
  height: 30px;

}
.drawPanel {
  margin: auto;
  margin-bottom: 6px;
  border-radius: 10px;
}

.messageBox {
  margin: 10px;
  height: 30px;
  width: 200px;
  border-radius: 5px;
  font-size: 1em;
  color: #374785;
  font-family: 'Acme', sans-serif;
  background-color:rgba(83, 78, 79, 0.49);
}

.answer {
  width: 70px;
  height: 30px;
  background-color: #24305e;
  font-size: 1em;
  font-family: 'Acme', sans-serif;
  color: white;
  border-radius: 5px;
}
.answer:hover{
  color: #f8e9a1;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.success {
  font-size: 10em;
  text-align: center;
  vertical-align: middle;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: #f76c6c;
}

.content{
  position: relative;
  min-height: 100vh;
}
.header {
  background-color: #24305e;
  border-radius: 10px;
}

.header > h1 {
  margin: 0 auto;
  padding-top: 10px;
  text-align: center;
  font-size: 2.5em;
  color: white;
}

.routesNav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.navLinks {
  margin: 10px;
  color: white;
  text-decoration: none;
  font-size: 1.3em;
}

.navLinks:hover {
  color: #f8e9a1;
}

.components{
  position: relative;
  padding-bottom: 2.5em;
  width: 75%;
  margin: auto;
}
footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  /* margin-top: 16vh; */
  /* padding-left: 10px; */
  color: white;
  background-color: #f76c6c;
  border-radius: 5px;
}

